import React from 'react';

const CommentsList = ({ comments }) => (
    <React.Fragment >
        <div className="comment">
            <h3>Comments :</h3>
            {
                comments.map((comment, key) => (
                    <div >
                        <h4>{comment.username}</h4>
                        <p className="para-italic">{comment.text}</p>
                    </div>
                ))
            }
        </div>

    </React.Fragment>
);

export default CommentsList;